	module.exports = {"data":{"markdownRemark":{"headings":[],"html":"<p>\n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 500px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 75%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"The opening scene.\"\n        title=\"\"\n        src=\"/static/advance_topics03_mv-647322706d0d82f0042336605fa58ba7-bc86b.png\"\n        srcset=\"/static/advance_topics03_mv-647322706d0d82f0042336605fa58ba7-c3a8a.png 125w,\n/static/advance_topics03_mv-647322706d0d82f0042336605fa58ba7-54de6.png 250w,\n/static/advance_topics03_mv-647322706d0d82f0042336605fa58ba7-bc86b.png 500w,\n/static/advance_topics03_mv-647322706d0d82f0042336605fa58ba7-da458.png 750w,\n/static/advance_topics03_mv-647322706d0d82f0042336605fa58ba7-b8502.png 1000w,\n/static/advance_topics03_mv-647322706d0d82f0042336605fa58ba7-34ab4.png 1500w,\n/static/advance_topics03_mv-647322706d0d82f0042336605fa58ba7-5cbc3.png 2000w\"\n        sizes=\"(max-width: 500px) 100vw, 500px\"\n      />\n    </span>\n  </span>\n  </p>\n<p>An experimental journey that explores a narrative through a poetic format using 3d visual art forms. By fabricating a 3d space, the visual art forms transcends both emotions and expression. By moving from space to space, users will experience a different emotion in asynchronous order that is all about exploring curiosity and embracing the unknown. Users will be able to navigate through the spaces while having accompanying music and spoken narrative played within each different space. </p>\n<p>(Based on the book Poetics of Space by Gaston Bachelard)</p>\n<p>\n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 500px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 75%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"One of the spaces when leaving the beginning. \"\n        title=\"\"\n        src=\"/static/advance_topics01_mv-443f239d6285701ebeddd81713bb8367-bc86b.png\"\n        srcset=\"/static/advance_topics01_mv-443f239d6285701ebeddd81713bb8367-c3a8a.png 125w,\n/static/advance_topics01_mv-443f239d6285701ebeddd81713bb8367-54de6.png 250w,\n/static/advance_topics01_mv-443f239d6285701ebeddd81713bb8367-bc86b.png 500w,\n/static/advance_topics01_mv-443f239d6285701ebeddd81713bb8367-da458.png 750w,\n/static/advance_topics01_mv-443f239d6285701ebeddd81713bb8367-b8502.png 1000w,\n/static/advance_topics01_mv-443f239d6285701ebeddd81713bb8367-34ab4.png 1500w,\n/static/advance_topics01_mv-443f239d6285701ebeddd81713bb8367-5cbc3.png 2000w\"\n        sizes=\"(max-width: 500px) 100vw, 500px\"\n      />\n    </span>\n  </span>\n  </p>\n<p>\n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 500px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 75%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"Another perspective is viewed when holding the space bar.\"\n        title=\"\"\n        src=\"/static/advance_topics04_mv-5825e3a842433c2edf15b81ea0fdf727-bc86b.png\"\n        srcset=\"/static/advance_topics04_mv-5825e3a842433c2edf15b81ea0fdf727-c3a8a.png 125w,\n/static/advance_topics04_mv-5825e3a842433c2edf15b81ea0fdf727-54de6.png 250w,\n/static/advance_topics04_mv-5825e3a842433c2edf15b81ea0fdf727-bc86b.png 500w,\n/static/advance_topics04_mv-5825e3a842433c2edf15b81ea0fdf727-da458.png 750w,\n/static/advance_topics04_mv-5825e3a842433c2edf15b81ea0fdf727-b8502.png 1000w,\n/static/advance_topics04_mv-5825e3a842433c2edf15b81ea0fdf727-34ab4.png 1500w,\n/static/advance_topics04_mv-5825e3a842433c2edf15b81ea0fdf727-5cbc3.png 2000w\"\n        sizes=\"(max-width: 500px) 100vw, 500px\"\n      />\n    </span>\n  </span>\n  </p>\n<p>Check out the full project and video at mivuu.co</p>","frontmatter":{"title":"The Traveller and Vivid Spaces","graduate":"Michelle Vu","category":"Web Design","category2":"Experiential Design","category3":null,"image":{"childImageSharp":{"sizes":{"base64":"data:image/png;base64,","aspectRatio":1.5,"src":"/static/advance_topics02_mv-57ebe619a47d085376d64e3aac6c4b6c-0f681.png","srcSet":"/static/advance_topics02_mv-57ebe619a47d085376d64e3aac6c4b6c-c1674.png 375w,\n/static/advance_topics02_mv-57ebe619a47d085376d64e3aac6c4b6c-34f47.png 750w,\n/static/advance_topics02_mv-57ebe619a47d085376d64e3aac6c4b6c-0f681.png 1500w,\n/static/advance_topics02_mv-57ebe619a47d085376d64e3aac6c4b6c-8123c.png 2000w","sizes":"(max-width: 1500px) 100vw, 1500px"}}}}}},"pathContext":{"slug":"/work/the-traveller-and-vibrant-surfaces/","name":"/The Traveller and Vivid Spaces/"}}

